import { left, right } from '../../Either/dist/effect-Either.esm.js';
import { dual, pipe } from '../../Function/dist/effect-Function.esm.js';
import { MissingData } from '../configError.esm.js';
import { of, isCons, cons } from '../../List/dist/effect-List.esm.js';
import { contains } from '../../Option/dist/effect-Option.esm.js';
import { head, tailNonEmpty, prepend, map } from '../../ReadonlyArray/dist/effect-ReadonlyArray.esm.js';

/** @internal */
const empty = {
  _tag: "Empty"
};

/** @internal */
const andThen = /*#__PURE__*/dual(2, (self, that) => ({
  _tag: "AndThen",
  first: self,
  second: that
}));

/** @internal */
const mapName = /*#__PURE__*/dual(2, (self, f) => andThen(self, {
  _tag: "MapName",
  f
}));

/** @internal */
const nested = /*#__PURE__*/dual(2, (self, name) => andThen(self, {
  _tag: "Nested",
  name
}));

/** @internal */
const unnested = /*#__PURE__*/dual(2, (self, name) => andThen(self, {
  _tag: "Unnested",
  name
}));

/** @internal */
const patch = /*#__PURE__*/dual(2, (path, patch) => {
  let input = of(patch);
  let output = path;
  while (isCons(input)) {
    const patch = input.head;
    switch (patch._tag) {
      case "Empty":
        {
          input = input.tail;
          break;
        }
      case "AndThen":
        {
          input = cons(patch.first, cons(patch.second, input.tail));
          break;
        }
      case "MapName":
        {
          output = map(output, patch.f);
          input = input.tail;
          break;
        }
      case "Nested":
        {
          output = prepend(output, patch.name);
          input = input.tail;
          break;
        }
      case "Unnested":
        {
          const containsName = pipe(head(output), contains(patch.name));
          if (containsName) {
            output = tailNonEmpty(output);
            input = input.tail;
          } else {
            return left(MissingData(output, `Expected ${patch.name} to be in path in ConfigProvider#unnested`));
          }
          break;
        }
    }
  }
  return right(output);
});

export { andThen, empty, mapName, nested, patch, unnested };
