import * as Cause from 'effect/Cause';
import * as Exit from 'effect/Exit';

/**
 * @since 1.0.0
 */

/**
 * @category model
 * @since 1.0.0
 */

/**
 * @category teardown
 * @since 1.0.0
 */
const defaultTeardown = (exit, onExit) => {
  onExit(Exit.isFailure(exit) && !Cause.isInterruptedOnly(exit.cause) ? 1 : 0);
};

/**
 * @category model
 * @since 1.0.0
 */

export { defaultTeardown };
